.self-wrap{padding:5px 5px 15px 5px;position:relative;overflow:hidden}.self-wrap .-header{position:relative;left:calc(100% - 50px);display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background-color:#905;height:50px;width:50px;border-radius:50%;font-weight:bolder}.self-wrap .-msg{background-color:#fff;padding:10px;float:right;position:relative;left:-60px;top:0;border-radius:6px;max-width:calc(100% - 180px);font-size:16px}.self-wrap .-msg:before{content:'';display:inline-block;height:0;width:0;border-left:solid 10px #fff;border-top:solid 6px transparent;border-bottom:solid 6px transparent;position:absolute;top:5px;left:100%}


