.other-wrap{padding:5px 5px 0 5px;position:relative}.other-wrap .-header{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background-color:#905;height:50px;width:50px;border-radius:50%;font-weight:bolder}.other-wrap .-name{position:absolute;font-size:14px;padding:2px;margin:0 10px;color:#999}.other-wrap .-msg{background-color:#fff;padding:10px;position:relative;left:60px;top:-20px;border-radius:6px;max-width:calc(100% - 180px);font-size:16px}.other-wrap .-msg:before{content:'';display:inline-block;height:0;width:0;border-right:solid 10px #fff;border-top:solid 6px transparent;border-bottom:solid 6px transparent;position:absolute;top:5px;left:-8px}


