.login-wrapper{background-color:#fff;position:absolute;top:0;left:-100%;right:100%;bottom:0}.login-wrapper .header{width:100%;height:30%;max-height:300px;background:url(../assets/1.jpg);background-size:contain}.login-wrapper .body{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;padding:20px}.login-wrapper .body .-title{height:80px;font-size:18px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-weight:bolder;color:#999}.login-wrapper .body .-name,.login-wrapper .body .-weichat{display:-webkit-box;display:-ms-flexbox;display:flex;height:40px;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:16px;color:#999;border-top:solid 1px rgba(0,0,0,0.05)}.login-wrapper .body .-name lable,.login-wrapper .body .-weichat lable{color:#999}.login-wrapper .body .-name input,.login-wrapper .body .-weichat input{box-sizing:border-box;height:25px;border:none;outline:none;padding:5px;color:#666;background-color:rgba(0,0,0,0.02)}.login-wrapper .body .-login{display:-webkit-box;display:-ms-flexbox;display:flex;height:80px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-size:16px;color:#999}.login-wrapper .body .-login .-btn{background-color:#3CAF36;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;width:80%;height:40px;margin:10px auto;color:#fff;font-weight:bolder}.login-wrapper .foot{height:80px;width:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;font-size:13px;color:#999}.login-wrapper .foot span{display:inline-block}.login-wrapper .foot span:first-child{font-size:15px}.login-wrapper a{color:#999}.loginslide-transition{-webkit-transition:all .3s ease;transition:all .3s ease;-webkit-transform:translate(100%, 0);transform:translate(100%, 0)}.loginslide-leave,.loginslide-enter{-webkit-transform:translate(0, 0);transform:translate(0, 0)}


